@import "src/styles/mixins";
@import "src/styles/variables";

.header {
  @include flex-center;
  width: 100%;
  height: $header-height;
  background-color: $secondary-dark;
  position: fixed;
  top: 0;
  z-index: $z-index-overlay;
  &__content {
    @include justify-center-between;
    width: 100%;
    padding: 40px;
    box-sizing: border-box;
    .language {
      cursor: pointer;
      font-weight: bold;
      dt,dd {
        display: inline-block; }
      dd {
        margin-left: .2rem; } }

    @media #{$mobile-width} {
      padding-inline: 20px; } } }
