@import "src/styles/mixins";
@import "src/styles/variables";

.icon_button {
  @include flex-center;
  padding: 8px 16px;
  border: none;
  border-radius: 8px;
  background-color: $secondary-light !important;
  position: relative;
  cursor: pointer;
  transition: 0.3s;

  span {
    margin-left: 8px;
    color: $main-dark; }

  img {
    height: 16px;
    object-fit: contain; }

  @media(hover: hover) {
    &:hover {
      padding: 10px 20px;
      border: 1px solid $secondary-light; } } }
